//收藏的reducer
let shoucang=[]
const shoucangreducer = (state =shoucang, action) => {
    // console.log(action);
    // switch (action.type) {
    //     case 'ADD':
            
    //         return [...state,action.shoucang];//返回收藏了的和新的收藏的
    //     default:
    //         return state;

    // }
    if(action.type==='ADD'){
        // console.log(action.shoucang);
        // console.log(state)
        // console.log(state.indexOf(action.shoucang))
        // console.log(state.indexOf(action.shoucang)<0);

            if(state.indexOf(action.shoucang)<0){
                return [...state,action.shoucang];//返回收藏了的和新的收藏的
            }
            
            else{
                return state;
            }
            

    }
    if(action.type === 'DELETE'){
        console.log('delete');
        let newState = JSON.parse(JSON.stringify(state));//删除数据
        newState.splice(action.index,1) 
        console.log(newState);
        return newState;
    }
    return state;
}

export default shoucangreducer