import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
//底部固定栏
class Tab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: '/home',
            hidden: false,
            fullScreen: false,
            
        };
    }


    render() {

        console.log( Boolean(this.props.id.indexOf(this.props.idx)+1)?true:this.state.selectedTab==='blue');
        const pathname = this.props.location.pathname;
        return (
            <div style={{
                position: 'fixed',
                bottom: 0,
                width: '100%',
            }} >
                {/* { this.props.id?this.setState({selectedTab:'blue'}):console.log(1) } */}
                
                

                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#33A3F4"
                    barTintColor="white"
                    hidden={false}
                >
                    {/* 每个页面以及里面的效果 */}
                    <TabBar.Item
                        title="店铺"
                        key="Life"
                        icon={<i className="iconfont iconhome-o"></i>
                        }
                        selectedIcon={<i className="iconfont iconshouye-yes"></i>

                        }
                        // 匹配子路由
                        selected={pathname === '/home'}
                        // badge={1}右上角显示
                        onPress={() => {
                            this.props.history.push('/home')
                        }}
                        data-seed="logId"
                    >


                    </TabBar.Item>

                    <TabBar.Item
                        icon={
                            <i className="iconfont  iconshoucang"></i>
                        }
                        selectedIcon={
                            <i className="iconfont iconstar-opp"></i>
                        }
                        title="收藏"
                        key="Koubei"
                        // badge={'new'}
                        // selected={this.state.selectedTab==='blue'}
                        selected={
                            Boolean(this.props.id.indexOf(this.props.idx)+1)?true:this.state.selectedTab==='blue'
                            // [1,2].indexOf(3)?true:this.state.selectedTab==='blue'
                      
                        }
                        
                        onPress={() => {
                            
                            if(this.props.username){
                                this.props.dispatch({
                                    type:'ADD',
                                    shoucang:this.props.idx
                                })
                                this.setState({selectedTab:'blue'})
                                
                            }
                       else{this.props.history.push('/login')}
                        }}
                        data-seed="logId1"
                    >

                    </TabBar.Item>
                    {/* 每个页面以及里面的效果 */}

                    <TabBar.Item
                        icon={
                            <i className="iconfont icondingdanliebiao"></i>
                        }
                        selectedIcon={
                            <i className="iconfont  icondingdanliebiao_s"></i>
                        }
                        title="客服"
                        key="Friend"
                        dot
                        selected={pathname === '/list'}
                        onPress={() => {

                            this.props.history.push('/list')

                        }}
                    >

                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className="iconfont  iconme"></i>}
                        selectedIcon={<i className="iconfont  iconIcon_wodeB"></i>}
                        title="加入购物车"
                        key="my"
                        selected={pathname === '/personal'}
                        onPress={() => {
                            this.props.history.push('/personal')

                        }}
                    >

                    </TabBar.Item>

                    <TabBar.Item
                        icon={<i className="iconfont  icongengduo1"></i>}
                        selectedIcon={<i className="iconfont  icongengduo"></i>}
                        title="立即购买"
                        key="my"
                        selected={pathname === '/more'}
                        onPress={() => {

                            this.props.history.push('/more')

                        }}
                    >

                    </TabBar.Item>




                </TabBar>
            </div>
        );
    }
}
const mapStateToProps = (state) => {
    return {
        username: state.name.userinfo,
        id:state.shoucang

    }
}

export default connect(mapStateToProps)(withRouter(Tab))
