//登陆的reducer
const loginreducer = (state={userinfo:''},action) => {
    // console.log(action);
    switch(action.type){
     case 'LOGIN':
         //Object.assign(state,{},{username:'123'});//复制一个对象
         return {userinfo:action.username};
     default:
         return state;
 
    }
 }
 
 export default  loginreducer