//图片列表的reducer
const getListredecer = (state = { datalist: [] }, action) => {
   
    switch (action.type) {
        case 'GETLIST':
            //Object.assign(state,{},{username:'123'});//复制一个对象
            // console.log(action.datalist);
            return action.datalist ;//返回接口图片数组
        default:
            return state;

    }
}

export default getListredecer