import React from 'react'
import { connect } from 'react-redux'
import {login} from './actionCreators'

const login1 = (props) => {
const log=()=>{
   
        props.dispatch(login())
 
}
    return (
        <div>
      {props.username ?
                <div>
                   
                    {props.username}
                    <button onClick={ ()=>{
                        props.dispatch({
                            type:'LOGIN',
                            username:''
                        })
                    }}>退出</button>
                </div>
                : <button onClick={log}
                >登录</button>
            }
                <button onClick={()=>{props.history.push('./home')}}>返回</button>
                </div>
                
            
    )
}

const mapStateToProps = (state) => {
    return {
        username: state.name.userinfo
    }
}

export default connect(mapStateToProps)(login1)
